import Web3 from "web3";
import config from "../config/config";

import WalletConnectProvider from "@walletconnect/web3-provider";


export async function connection() {
  var connect = {
    web3: "",
    address: "",
    network: 0,
  };
  var web3 = null;

  if (localStorage.getItem("iswallet") === "walletconnect") {
    const provider = new WalletConnectProvider({
      rpc: {
        [config.NetworkId]: config.netWorkUrl
      },
    });
    await provider.enable();
    web3 = new Web3(provider);
  } else if (window.ethereum && typeof window.ethereum !== "undefined") {
    web3 = new Web3(window.ethereum); //trust wallet and metamask
    if (localStorage.getItem("isRequested") !== "yes") {
      localStorage.setItem("isRequested", "yes");
      window.ethereum.request({ method: 'eth_requestAccounts' });
    }
  }

  try {
    if (typeof web3 !== "undefined") {

      var network = await web3.eth.net.getId();
      var result = await web3.eth.getAccounts();

      var currAddr = result[0];
      if (currAddr === undefined) currAddr = "";
      if (network === config.NetworkId) {
        connect.network = network;
        connect.web3 = web3;
        connect.address = currAddr;

      }
    }
  } catch (err) { }


  return connect;
}
