import React from 'react'
import { useDispatch } from 'react-redux';
import Web3 from 'web3';
import WalletConnectProvider from "@walletconnect/web3-provider";
import { isMobile } from "react-device-detect";
import $ from "jquery"
import { setWallet } from "../reducers/Actions";
import { toastAlert } from "../helper/toastAlert";
import config from "../config/config";
import { connection } from "../helper/connection"
import { checkUser } from "../Api/UserActions"
const WalletModal = (props) => {

  const dispatch = useDispatch();

  async function connectWallet(type) {
    console.log(type, 'type-type')
    try {

      window.$('#wallet_modal').modal('hide');
      var web3 = null;
      var provider = null;
      if (isMobile) {
        if (type === "walletconnect") {
          provider = new WalletConnectProvider({
            rpc: {
              [config.NetworkId]: config.netWorkUrl
            },
            chainId: config.NetworkId
          });
          await provider.enable();
          web3 = new Web3(provider);
        } else if (type === "metamask" || type === "trustwallet") {

          if (window.ethereum && typeof window.ethereum !== "undefined") {
            web3 = new Web3(window.ethereum);
          } else {
            toastAlert('error', "Please login into metamask", 'balance');
            return;
          }
        } else {
          toastAlert('error', "Oops something went wrong", 'balance');
          return;
        }

      } else {

        if (type === "walletconnect") {

          provider = new WalletConnectProvider({
            rpc: {
              [config.NetworkId]: config.netWorkUrl
            },
            chainId: config.NetworkId
          });
          provider.on("connect", () => {
          });
          await provider.enable();

          web3 = new Web3(provider);

        } else if (type === "metamask" || type === "trustwallet") {
          if (window.ethereum && typeof window.ethereum !== "undefined") {
            console.log('1111111111111')
            web3 = new Web3(window.ethereum);
            console.log(window.ethereum.isConnected(), 'window.ethereum.isConnected()')
            try {
              if (!window.ethereum.isConnected()) {
                setTimeout(() => {
                  window.location.reload();
                }, 500);
                await window.ethereum.request({ method: 'eth_requestAccounts' });
              }
              console.log('2222222222')
              await window.ethereum.request({ method: 'eth_requestAccounts' });
            } catch (err) {
              console.log('3333333333', err)
              toastAlert('error', "Please login to your metamask account", 'balance');

            }

            if (type === "metamask") {
              console.log('4444444444')
              await web3.currentProvider.request({
                method: "wallet_switchEthereumChain",
                params: [{ chainId: '0x' + (config.NetworkId).toString(16) }]
              }).catch(async (err) => {
                console.log('555555555', err)
                if (err.code === 4902) {
                  await window.ethereum.request({
                    method: 'wallet_addEthereumChain',
                    params: [{
                      chainId: '0x' + (config.NetworkId).toString(16),
                      chainName: 'Ethereum Mainnet',
                      nativeCurrency: {
                        name: 'Ethereum Coin',
                        symbol: 'ETH',
                        decimals: 18
                      },
                      rpcUrls: [config.netWorkUrl],
                      blockExplorerUrls: [config.blockexploer]
                    }]
                  }).then(async () => {
                    await connectNetwork();
                  }).catch((error) => {
                    console.log('66666666666', error)
                  })
                }
              });


            }
          } else {
            toastAlert('error', "Please login into metamask", 'balance');
          }
        } else {
          toastAlert('error', "Oops something went wrong", 'balance');
          return;
        }
      }
      await connectNetwork();
    } catch (err) {
      console.log('77777777')

    }

    async function connectNetwork() {
      var network = await web3.eth.net.getId();
      var get = await connection();
      if (config.NetworkId === network) {
        var data = {
          connect: "yes",
          iswallet: type,
          network: get.network,
          web3: get.web3,
          address: get.address,
        }
        let reqdata = { address: data && data.address ? data.address : '' };
        let { status } = await checkUser(reqdata);
        console.log(status, 'rtttttttttttttt')
        if (status == true) {
          toastAlert('error', "Your Address is Blocked");
        }
        else {
          localStorage.setItem("connect", "yes");
          localStorage.setItem("iswallet", type);
          dispatch(setWallet(data));
          console.log(data, 'daattttttttt')
        }





        //header address menu close issues
        $(".wallet_add").click()
      } else {
        toastAlert('error', "please select " + config.networkName + " network on your wallet", 'network');
        if (type === "walletconnect") {
          try {
            await provider.disconnect();
          } catch (err) {

          }

        }
      }
    }


  }

  return (
    <div className="modal fade primary_modal" id="wallet_modal" tabIndex="-1" role="dialog" aria-labelledby="wallet_modal" aria-hidden="true">
      <div className="modal-dialog modal-dialog-centered" role="document">
        <div className="modal-content">
          <div className="modal-header">
            <h5 className="modal-title" id="wallet_modal">Connect to a wallet</h5>
            <button type="button" className="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div className="modal-body">
            <div className="wallet_panel_div">
              <div className="wallet_panel" onClick={() => { connectWallet("metamask") }}>
                <img src={require("../assets/images/metamask_icon.png")} alt="Icon" className="img-fluid" />
                <h2>Metamask</h2>
              </div>

              <div className="wallet_panel" onClick={() => { connectWallet("walletconnect") }}>
                <img src={require("../assets/images/trustwallet_icon.png")} alt="Icon" className="img-fluid" />
                <h2>Trust Wallet</h2>
              </div>
            </div>
            {/* <div className="wallet_modal_footer">
              <a href="#/" target="_blank"><HelpOutline className="mr-2" /><span>Learn how to connect</span></a>
            </div> */}
          </div>
        </div>
      </div>
    </div>
  )
}

export default WalletModal;